import list_item from "../../../list_item";
import f_button from "../../../button";
export default {
  name: "report_today",
  components: {
    list_item,
    f_button,
  },
  data() {
    return {
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      list: [
        {
          title: "2019-01-05",
          type: "",
          time: "12:12:21",
          status: "",
          status_color:'red',
          link: "/end_clock_in"
        },
        {
          title: "2019-01-05",
          type: "",
          time: "12:12:21",
          status: "",
          status_color:'blue',
          link: "/clock_in_details"
        },
        {
          title: "2019-01-05",
          type: "",
          time: "12:12:21",
          status: "",
          status_color:'red',
          hide_border:true,
          link: "/end_clock_in"
        }
      ]
    };
  },
  methods: {
    call_back(res) {
      this.go_page('/add_clock_in')
    },
  
    go_page(url) {
      this.$router.push(url);
    }
  }
};



