export default {
  props: ["upload_file_info"],
  data() {
    return {
      show_list: false,
      file_list: [{ name: "1", url: "../assets/images/4.png" }]
    };
  },
  methods: {
    upload_suc(res, file) {
      this.$emit("call_back", URL.createObjectURL(file.raw));
    }
  }
};



