import list_item from "@/components/list_item";
import f_button from "@/components/button";
import f_search from "@/components/search";
import f_nav_bar from "@/components/nav_bar";
import actionsheet from "@/components/actionsheet";
export default {
  name: "report_today",
  components: {
    list_item,
    f_button,
    f_search,
    f_nav_bar,
    actionsheet
  },
  data() {
    return {
      actionsheet_info: {
        show: false,
        list: {
          menu1: "",
          menu2: "",
          menu3: ""
        },
        call_back: true
      },
      search: {
        placeholder: ""
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      nav_bar: {
        list: [
          {
            cur: true,
            text: ""
          }
        ]
      },

      list: [
        {
          title: "2019-01-05",
          type: "",
          time: "2019-01-05 12:12:21",
        },
        {
          title: "2019-01-05",
          type: "",
          time: "2019-01-05 12:12:21",
        },
        {
          title: "2019-01-05",
          type: "",
          time: "2019-01-05 12:12:21",
          hide_border:true,
        }
      ]
    };
  },
  methods: {
    call_back(res) {
      this.actionsheet_info.show = true;
    },

    go_page(url) {
      this.$router.push(url);
    }
  }
};



