import edit_input from "../../../edit_input";
import f_button from "../../../button";
export default {
  components: {
    edit_input,
    f_button,
  },
  data() {
    return {
      edit_input_list: [
        { read: true, v: " ", k: " " },
        { read: true, v: "", k: "" }
      ],
      edit_input_list1: [
        { read: true, v: "", k: "001" },
        { read: true, v: "", k: "2019-01-01" },
        { read: true, v: "", k: "2019-01-01" },
        { read: true, v: " ", k: " " }
