import edit_input from "../../../../edit_input";
import f_button from "../../../../button";
import edit_time from "../../../../edit_time";
import edit_select from "../../../../edit_select";
import upload_file from "../../../../upload_file";
import nav_bar from "../../../../nav_bar";
import result from "../../../../result";
import top_progress from "../../../../top_progress";
import f_confirm from "../../../../confirm";
export default {
  components: {
    top_progress,
    edit_input,
    nav_bar,
    edit_time,
    upload_file,
    f_button,
    edit_select,
    f_confirm,
    result,
  },
  data() {
    return {
      nav_bar_idx: -1,
      top_progress: {
        step: 0,
        list: [{ des: "" }, { des: "" }, { des: "" }]
      },
      edit_input_list: [{ required: true, v: "", k: "" }],
      edit_input_list1: [
        { required: true, v: "", k: "" },
        { required: true, v: "", k: "" },
        { required: true, v: "", k: "" },
        { v: "", k: "" , hide_border:true}
      ],
      edit_input_list2: [
        { v: "", k: "" },
        { v: "", k: "" },
        { v: "", k: "" },
        { v: "", k: "", read: true , hide_border:true}
      ],
      edit_input_list3: [{ v: "", k: "" }],
      edit_input_list4: [
        { v: "", k: "" },
        { v: "", k: "" , hide_border:true}
      ],
      edit_input_list5: [
        { v: "", k: "" },
        { v: "", k: "", hide_border:true }
      ],
      upload_file_info: {
        text: "",
        required: true,
        class: "img40",
      },
      edit_time_list: [
        { title: "", default_time: "" }
      ],
      edit_time_list1: [
        { title: "", default_time: "" },
        { title: "", default_time: "" }
      ],
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      nav_bar: {
        list: [
          {
            text: "",
            cur: true
          },
          {
            text: "",
            cur: false
          },
          {
            text: "",
            cur: false
          },
          {
            text: "",
            cur: false
          }
        ]
      },
      edit_select: [
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "1", value: "1" },
            { key: "", value: "" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        }
      ],
      edit_time_list2: [
        { title: "", default_time: "" }
      ],
      edit_input_list6: [{ v: "", k: "" , hide_border:true}],
      confirm_info:{
        title:'',
        content:'',
        show:false
      },
      result_info: {
        title: "",
        des:
          '<a href="javascript:void(0);">EO1809231814196051</a>',
        buttons: [
          {
            type: "primary",
            text: "",
            link: "/",
          },
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "default",
            text: "",
            link: "/"
          }
        ]
      },
    };
  },
  methods: {
    get_nav_bar(idx) {
      this.nav_bar_idx = idx;
      for (var i in this.nav_bar.list) {
        this.nav_bar.list[i].cur = false;
      }
      if (idx == 3) {
        this.button_list[0].text = "";
      } else {
        this.button_list[0].text = "";
      }
      this.nav_bar.list[idx].cur = true;
    },
    get_call_back() {
      var text_val = this.button_list[0].text;
      if (text_val != "") {
        this.top_progress.step = 1;
        if (this.top_progress.step == 1 && this.nav_bar_idx < 3) {
          this.nav_bar_idx = this.nav_bar_idx += 1;

          this.nav_bar.list[this.nav_bar_idx].cur = true;
          if (this.nav_bar_idx == 3) {
            this.button_list[0].text = "";
          } else {
            this.button_list[0].text = "";
          }
        }
      }else{
        this.confirm_info.show=true
      }
    },
    er_code_back(idx) {
      if (idx == 3) {
        console.log("");
      }
    },
    confirm_back(res){
     this.top_progress.step=2
    },
    go_page(url){
      this.$router.push(url)
    }
  }
};



