import top_progress from "../../../top_progress";
import edit_input from "../../../edit_input";
import edit_select from "../../../edit_select";
import edit_time from "../../../edit_time";
import edit_radio from "../../../edit_radio";
import edit_textarea from "../../../edit_textarea";
import f_button from "../../../button";
import nav_bar from "../../../nav_bar";
import loading from "../../../loading";
import result from "../../../result";
import f_confirm from "../../../confirm";
export default {
  name: "online_report",
  components: {
    top_progress,
    edit_input,
    edit_select,
    edit_time,
    edit_radio,
    edit_textarea,
    f_button,
    nav_bar,
    loading,
    result,
    f_confirm
  },
  data() {
    return {
      top_progress: {
        step: 0,
        list: [{ des: "" }, { des: "" }, { des: "" }]
      },
      top_title: "",
      edit_select: [
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "1", value: "1" },
            { key: "2", value: "2" }
          ]
        }
      ],
      edit_select1: [
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "", value: "" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [{ key: "", value: "" }, { key: "", value: "" }]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [{ key: "", value: "" }, { key: "", value: "" }]
        }
      ],
      edit_time_list: [
        { title: "", default_time: "" },
        { title: "", default_time: "" }
      ],
      edit_radio: { title: "", required: true, list: ["", ""] },
      edit_input_list: [
        { required: true, v: "", k: "" }
      ],
      edit_input_list1: [
        { v: "", k: "" },
        { v: "", k: "" }
      ],
      edit_textarea: {
        title: "",
        required: true,
        placeholder: ""
      },
      edit_textarea1: {
        title: "",
        placeholder: ""
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      nav_bar: {
        list: [
          {
            text: "",
            cur: true
          },
          {
            text: ""
          }
        ]
      },
      edit_input_list2: [
        { v: "", k: "", hide_border: true }
      ],
      step_2_nav_bar_idx: 0,
      loading_info: {
        show: false,
        text: ",.."
      },
      result_info: {
        title: "",
        des:
          '<a href="javascript:void(0);">EO1809231814196051</a>',
        buttons: [
          {
            type: "primary",
            text: "",
            link: "BACK"
          },
          {
            type: "primary",
            text: "",
            link: "/report_today"
          },
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "default",
            text: "",
            link: "/"
          }
        ]
      },
      confirm_info: {
        show: false,
        title: "",
        content: ""
      }
    };
  },
  methods: {
    get_call_back(res) {
      var step = this.top_progress.step;
      if (step == 0) {
        this.top_progress.step = 1;
        this.top_title = "";
        this.go_top();
        this.button_list = [
          {
            type: "primary",
            text: "",
            call_back: true
          }
        ];
      } else if (step == 1) {
        this.confirm_info.show = true;
      }
    },
    go_top() {
      window.scroll(0, 0);
    },
    get_nav_bar(idx) {
      if (idx == 1) {
        this.edit_input_list2 = [
          {
            v: "",
            k: "",
            hide_border: true
          }
        ];
      } else {
        this.edit_input_list2 = [
          { v: "", k: "", hide_border: true }
        ];
      }
    },
    confirm_back(res) {
      this.loading_info.show = true;
      setTimeout(() => {
        this.loading_info.show = false;
        this.top_progress.step = 2;
      }, 1000);
    },
    go_page(url) {
      console.log(url);
      this.$router.push(url);
    }
  }
};



