import f_button from "../../../../button";
import edit_select from "../../../../edit_select";
import edit_textarea from "../../../../edit_textarea";
import result from "../../../../result";
import top_progress from "../../../../top_progress";
import f_confirm from "../../../../confirm";
import f_table from "../../../../table";
export default {
  components: {
    top_progress,
    f_button,
    edit_select,
    edit_textarea,
    f_confirm,
    f_table,
    result
  },
  data() {
    return {
      nav_bar_idx: -1,
      top_title: "",
      top_progress: {
        step: 0,
        list: [{ des: "" }, { des: "" }, { des: "" }]
      },
      edit_textarea: {
        required: true,
        title: "",
        placeholder: ""
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      edit_select: [
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "" ,min_w:true},
                { td: "", col: "3" },
                { td: "" }
              ]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border:true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "1",min_w:true }, { td: "" }, { td: "My34234" }]
            },
            {
              td_list: [
                { td: "10000" ,min_w:true},
                { td: "" },
                { td: "My34234" }
              ]
            },
            {
              td_list: [{ td: "1",min_w:true }, { td: "" }, { td: "My34234" }]
            }
          ]
        }
      },
      confirm_info: {
        title: "",
        content: "",
        show: false
      },
      result_info: {
        title: "",
        des:
          '<a href="javascript:void(0);">EO1809231814196051</a>',
        buttons: [
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "default",
            text: "",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    get_call_back() {
      var step = this.top_progress.step;
      if (step == 0) {
        this.top_progress.step = 1;
        this.top_title = "";
        this.button_list[0].text = "";
      } else if (step == 1) {
        this.confirm_info.show = true;
      }
    },
    confirm_back(res) {
      this.top_progress.step = 2;
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



