
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

export default {
name: "edit_select",
components: {
Group,
Selector
},
data() {
return {};
},
props: ["edit_select"],
created: function() {},
methods: {
onchange(val) {
this.$emit("select", val);
}
}
};



