import edit_input from "../../../../edit_input";
import f_button from "../../../../button";

export default {
  components: {
    edit_input,
    f_button
  },
  data() {
    return {
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      edit_input_list: [
        { read: true, v: "", k: "EO1809231" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "2019-01-12 09:12:12" },
        { read: true, v: "", k: "" ,hide_border:true}
      ],
      edit_input_list1: [
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "M7676720001" ,hide_border:true},
      ],
      edit_input_list2: [
        { read: true, v: "", k: "2019-01-12 09:23:12" },
        { read: true, v: "", k: "2019-01-12 09:23:12" ,hide_border:true},
      ]
    };
  },
  methods: {
    get_call_back() {
      this.go_page('/device_details')
    },
    go_page(url){
      this.$router.push(url)
    }
  }
};



