
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
XButton
},
data() {
return {
};
},
props: ["button_list"],
methods: {
click(index) {
var info = this.button_list[index];
if (info.call_back) {
this.$emit("call_back", "1");
}
},
go_page(url) {
this.$router.push(url);
}
}
};



