import list_item from "../../../../list_item";
import f_nav_bar from "../../../../nav_bar";
export default {
  name: "report_today",
  components: {
    list_item,
    f_nav_bar
  },
  data() {
    return {
      nav_bar: {
        list: [
          {
            cur: true,
            text: ""
          }
        ]
      },

      list: [
        {
          title: "",
          type: "",
          time: "M7676720001",
          status: "",
          status_color: "blue",
          link: "/device_check"
        },
        {
          title: "",
          type: "",
          time: "M7676720001",
          link: "/device_check_details "
        },
        {
          title: "",
          type: "",
          time: "M7676720001",
          status: "",
          status_color: "blue",
          link: "/device_check",
          hide_border: true
        }
      ]
    };
  },
  methods: {
    call_back(res) {
      this.go_page("/check_info");
    },

    go_page(url) {
      this.$router.push(url);
    }
  }
};



