import f_button from "@/components/button";
import edit_select from "@/components/edit_select";
import upload_file from "@/components/upload_file";
import f_confirm from "@/components/confirm";
import edit_input from "@/components/edit_input";
import result from "@/components/result";
export default {
  components: {
    edit_input,
    upload_file,
    edit_select,
    f_confirm,
    result,
    f_button
  },
  data() {
    return {
      show_result: false,
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      result_info: {
        title: "",
        des:
          '<a href="javascript:void(0);">EO1809231814196051</a>',
        buttons: [
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "default",
            text: "",
            link: "/"
          }
        ]
      },
      confirm_info: {
        show: false,
        title: "",
        content: "?"
      },
      edit_select: [
        {
          title: "",
          default_val: "",
          placeholder: "",
          w120:true,
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        }
      ],
      upload_file_info: {
        text: "",
        class: "img40",
        w120: true
      },
      upload_file_info1: {
        text: "",
        class: "img40",
        w120: true
      },
      upload_file_info2: {
        text: "",
        class: "img40",
        w120: true
      },
      upload_file_info3: {
        text: "",
        class: "img40",
        w120: true
      },
      edit_input_list: [
        { read: true, v: "", k: "" ,w120:true,},
        { read: true, v: "", k: "M0192123" ,w120:true,},
        { read: true, v: "", k: "", hide_border: true ,w120:true,}
      ]
    };
  },
  methods: {
    btn_call_back() {
      this.confirm_info.show = true;
    },
    go_page(url) {
      this.$router.push(url);
    },
    confirm_back() {
      this.show_result = true;
    }
  }
};



