import Vue from "vue";
import Router from "vue-router";
import work_bench from "@/components/page/worker/index";
import report_today from "@/components/page/worker/report_today/report_today";
import online_report from "@/components/page/worker/report_today/online_report";
import offline_report from "@/components/page/worker/report_today/offline_report";
import report_tomorrow from "@/components/page/worker/report_tomorrow/report_tomorrow";
import no_on_plan from "@/components/page/worker/report_tomorrow/no_on_plan";
import on_plan from "@/components/page/worker/report_tomorrow/on_plan";
import on_plan_details from "@/components/page/worker/report_tomorrow/on_plan_details";
import clock_in from "@/components/page/worker/clock_in/clock_in";
import add_clock_in from "@/components/page/worker/clock_in/add_clock_in";
import end_clock_in from "@/components/page/worker/clock_in/end_clock_in";
import clock_in_details from "@/components/page/worker/clock_in/clock_in_details";
import device_menagement from "@/components/page/worker/device_menagement/device_menagement";
import warehouse_in from "@/components/page/worker/device_menagement/warehouse_in/warehouse_in";
import add_device from "@/components/page/worker/device_menagement/warehouse_in/add_device";
import device_sm_details from "@/components/page/worker/device_menagement/warehouse_in/device_sm_details";
import device_details from "@/components/page/worker/device_menagement/warehouse_in/device_details";
import loop_list from "@/components/page/worker/device_menagement/warehouse_in/loop_list";
import loop_details from "@/components/page/worker/device_menagement/warehouse_in/loop_details";
import service_list from "@/components/page/worker/device_menagement/warehouse_in/service_list";
import service_details from "@/components/page/worker/device_menagement/warehouse_in/service_details";
import warehouse_out from "@/components/page/worker/device_menagement/warehouse_out/warehouse_out";
import add_device_out from "@/components/page/worker/device_menagement/warehouse_out/add_device_out";
import warehouse_out_details from "@/components/page/worker/device_menagement/warehouse_out/warehouse_out_details";
import check_list from "@/components/page/worker/device_menagement/check/check_list";
import check_info from "@/components/page/worker/device_menagement/check/check_info";
import use_list from "@/components/page/worker/device_menagement/my_use/use_list";
import use_details from "@/components/page/worker/device_menagement/my_use/use_details";
import add_use from "@/components/page/worker/device_menagement/use/add_use";
import return_device from "@/components/page/worker/device_menagement/return_device/return_device";
import return_details from "@/components/page/worker/device_menagement/my_use/return_details";
import my_check from "@/components/page/worker/device_menagement/my_check/my_check";
import device_check from "@/components/page/worker/device_menagement/my_check/device_check";
import device_check_details from "@/components/page/worker/device_menagement/my_check/device_check_details";
import review_list from "@/components/page/worker/device_menagement/review/review_list";
import device_info_list from "@/components/page/worker/device_menagement/device_info/device_info_list";
import person_device_list from "@/components/page/worker/device_menagement/device_info/person_device_list";
import device_details_list from "@/components/page/worker/device_menagement/device_details/device_details_list";
import warehouse_details_list from "@/components/page/worker/device_menagement/device_details/warehouse_details_list";
import video_meeting from "@/components/page/worker/video_meeting/video_meeting";
import know_how_list from "@/components/page/worker/know_how/know_how_list";
import know_how_details from "@/components/page/worker/know_how/know_how_details";
import notice_list from "@/components/page/worker/notice/notice_list";
import notice_details from "@/components/page/worker/notice/notice_details";
import status_menagement_list from "@/components/page/worker/device_menagement/status_menagement/status_menagement_list";
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "work_bench",
      component: work_bench
    },
    {
      path: "/report_today",
      name: "report_today",
      component: report_today
    },
    {
      path: "/online_report",
      name: "online_report",
      component: online_report
    },
    {
      path: "/offline_report",
      name: "offline_report",
      component: offline_report
    },
    {
      path: "/report_tomorrow",
      name: "report_tomorrow",
      component: report_tomorrow
    },
    {
      path: "/no_on_plan",
      name: "no_on_plan",
      component: no_on_plan
    },
    {
      path: "/on_plan",
      name: "on_plan",
      component: on_plan
    },
    {
      path: "/on_plan_details",
      name: "on_plan_details",
      component: on_plan_details
    },
    {
      path: "/clock_in",
      name: "clock_in",
      component: clock_in
    },
    {
      path: "/add_clock_in",
      name: "add_clock_in",
      component: add_clock_in
    },
    {
      path: "/end_clock_in",
      name: "end_clock_in",
      component: end_clock_in
    },
    {
      path: "/clock_in_details",
      name: "clock_in_details",
      component: clock_in_details
    },
    {
      path: "/device_menagement",
      name: "device_menagement",
      component: device_menagement
    },
    {
      path: "/warehouse_in",
      name: "warehouse_in",
      component: warehouse_in
    },
    {
      path: "/add_device",
      name: "add_device",
      component: add_device
    },
    {
      path: "/device_sm_details",
      name: "device_sm_details",
      component: device_sm_details
    },
    {
      path: "/device_details",
      name: "device_details",
      component: device_details
    },
    {
      path: "/loop_list",
      name: "loop_list",
      component: loop_list
    },
    {
      path: "/loop_details",
      name: "loop_details",
      component: loop_details
    },
    {
      path: "/service_list",
      name: "service_list",
      component: service_list
    },
    {
      path: "/service_details",
      name: "service_details",
      component: service_details
    },
    {
      path: "/warehouse_out",
      name: "warehouse_out",
      component: warehouse_out
    },
    {
      path: "/add_device_out",
      name: "add_device_out",
      component: add_device_out
    },
    {
      path: "/warehouse_out_details",
      name: "warehouse_out_details",
      component: warehouse_out_details
    },
    {
      path: "/check_list",
      name: "check_list",
      component: check_list
    },
    {
      path: "/check_info",
      name: "check_info",
      component: check_info
    },
    {
      path: "/use_list",
      name: "use_list",
      component: use_list
    },
    {
      path: "/use_details",
      name: "use_details",
      component: use_details
    },
    {
      path: "/add_use",
      name: "add_use",
      component: add_use
    },
    {
      path: "/return_device",
      name: "return_device",
      component: return_device
    },
    {
      path: "/return_details",
      name: "return_details",
      component: return_details
    },
    {
      path: "/my_check",
      name: "my_check",
      component: my_check
    },
    {
      path: "/device_check",
      name: "device_check",
      component: device_check
    },
    {
      path: "/device_check_details",
      name: "device_check_details",
      component: device_check_details
    },
    {
      path: "/review_list",
      name: "review_list",
      component: review_list
    },
    {
      path: "/device_info_list",
      name: "device_info_list",
      component: device_info_list
    },
    {
      path: "/person_device_list",
      name: "person_device_list",
      component: person_device_list
    },
    {
      path: "/device_details_list",
      name: "device_details_list",
      component: device_details_list
    },
    {
      path: "/warehouse_details_list",
      name: "warehouse_details_list",
      component: warehouse_details_list
    },
    {
      path: "/video_meeting",
      name: "video_meeting",
      component: video_meeting
    },
    {
      path: "/know_how_list",
      name: "know_how_list",
      component: know_how_list
    },
    {
      path: "/know_how_details",
      name: "know_how_details",
      component: know_how_details
    },
    {
      path: "/notice_list",
      name: "notice_list",
      component: notice_list
    },
    {
      path: "/notice_details",
      name: "notice_details",
      component: notice_details
    },
    {
      path: "/status_menagement_list",
      name: "status_menagement_list",
      component: status_menagement_list
    },

    {
      path: "/report",
      name: "report",
      component: () => import("@/components/page/report")
    },
    {
      path: "/tomorrowPlan",
      name: "tomorrowPlan",
      component: () => import("@/components/page/report/tomorrowPlan")
    },
    {
      path: "/offlinePlan/:id",
      name: "offlinePlan",
      component: () =>
        import("@/components/page/report/tomorrowPlan/offlinePlanDetails.vue")
    },
    {
      path: "/onlinePlan/:id",
      name: "onlinePlan",
      component: () =>
        import("@/components/page/report/tomorrowPlan/onlinePlanDetails.vue")
    },
    {
      path: "/allPlans",
      name: "allPlans",
      component: () =>
        import("@/components/page/report/tomorrowPlan/allPlans.vue")
    },
    {
      path: "/newOnlinePlan",
      name: "newOnlinePlan",
      component: () =>
        import("@/components/page/report/tomorrowPlan/newPlan/newOnlinePlan.vue")
    },
    {
      path: "/newOfflinePlan",
      name: "newOfflinePlan",
      component: () =>
        import("@/components/page/report/tomorrowPlan/newPlan/newOfflinePlan.vue")
    },
    {
      path: "/saveMsg",
      name: "saveMsg",
      component: () =>
        import("@/components/page/report/tomorrowPlan/saveMsg.vue")
    },
    {
      path: "/checkin",
      name: "checkin",
      component: () => import("@/components/page/report/checkin")
    },
    {
      path: "/allCheckin",
      name: "allCheckin",
      component: () => import("@/components/page/report/checkin/allCheckin.vue")
    },
    {
      path: "/reportList",
      name: "reportList",
      component: () => import("@/components/page/report/reportList")
    },
    {
      path: "/allReports",
      name: "allReports",
      component: () =>
        import("@/components/page/report/reportList/allReports.vue")
    },
    {
      path: "/offlineWork/:id",
      name: "offlineWork",
      component: () =>
        import("@/components/page/report/reportList/offlineWorkDetails.vue")
    },
    {
      path: "/onlineWork/:id",
      name: "onlineWork",
      component: () =>
        import("@/components/page/report/reportList/onlineWorkDetails.vue")
    }
  ]
});



