import { BmlCurveLine } from "vue-baidu-map";
export default {
  components: {
    BmlCurveLine
  },
  props: ["map_info"],
  data() {
    return {
      center: { lng: "116.40387397", lat: "39.91488908" },
      zoom: 15,
      text: "",
      storke_color:"#ff0000",
      points: [
        { lng: 116.40387397, lat: 39.91488908 },
      ]
    };
  },
  methods: {
    get_address(lng,lat){
      this.$http({
        method:'get',
        url:'http://api.map.baidu.com/geocoder/v2/?callback=renderReverse&location=35.658651,139.745415&output=json&pois=1&latest_admin=1&ak=8yxoChbHsCFzcT5vWZsqwryKmWDUOemj',
        data:{
          location:{
            lat:lat,
            lng:lng
          },
          coordtype:"gcj02ll",
          latest_admin:1,
          extensions_town:true,
          output:'json',
        }
      }).then(res=>{
        console.log(res)
        this.text = res
      })
    },
    handler({ BMap, map }) {
      let _this = this;
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(
        function(r) {
          console.log(r);
          var mk = new BMap.Marker(r.point);
          map.addOverlay(mk);
          map.panTo(r.point);
          _this.center = { lng: r.point.lng, lat: r.point.lat };
          _this.get_address(r.point.lng,r.point.lat)
        },
        { enableHighAccuracy: true }
      );
    },
    get_click_lbs(res){
      var lbs = res.point;
      this.points.push({lng:lbs.lng,lat:lbs.lat})
    },
    update(e){
      this.points = e.target.cornerPoints;
    }
  }
};



