import edit_input from "../../../../edit_input";
import f_button from "../../../../button";

export default {
  components: {
    edit_input,
    f_button
  },
  data() {
    return {
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      edit_input_list: [
        { read: true, v: "", k: "", no_arrow: true },
        { read: true, v: "M0192123", k: "" },
        { read: true, v: "", k: "", hide_border: true }
      ],
      edit_input_list1: [
        { read: true, v: "", k: "M0192123" },
        { read: true, v: "", k: "M019212309" },
        { read: true, v: "", k: "M0192123786", hide_border: true }
      ],
      edit_input_list2: [
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "M001091205" },
        { read: true, v: "", k: "RC240C" },
        { read: true, v: "", k: "001091205", hide_border: true }
      ],
      edit_input_list3: [
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "", hide_border: true }
      ],
      edit_input_list4: [
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "12" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "" },
        { read: true, v: "", k: "", hide_border: true }
      ],
      edit_input_list5: [
        { read: true, v: "", k: "", hide_border: true }
      ],
      edit_input_list6: [
        { read: true, v: "", k: "23" },
        { read: true, v: "", k: "2000" },
        { read: true, v: "", k: "1" },
        { read: true, v: "", k: "2000", hide_border: true }
      ],
      edit_input_list7: [
        { read: true, v: "", k: "12" },
        { read: true, v: "", k: "17", hide_border: true }
      ],
      edit_input_list8: [
        { read: true, v: "", k: "", hide_border: true }
      ],
      edit_input_list9: [
        { read: true, v: "", k: "2019-01-12 09:00:12" },
        {
          read: true,
          v: "",
          k: "2019-01-12 09:00:12",
          hide_border: true
        }
      ]
    };
  },
  methods: {
    get_call_back() {
      this.go_page("/add_device");
    },
    go_page(url) {
      this.$router.push(url);
    },
    input_back(res) {
      console.log(res);
      if (res == 0) {
        //
        this.go_page("/loop_list");
      } else if (res == 1) {
        //
        this.go_page("/service_list");
      }
    }
  }
};



