          }
        ]
      }
    };
  },
  methods: {
    btn_call_back(res) {
      this.picker_info.show = true;
    },
   picker_back(arr){
     console.log('picker',arr)
     if(arr[1]==""){
       this.go_page('/online_report')
     }else{
       this.go_page('/offline_report')
     }
   },
    confirm_back(res) {
      console.log("confirm", res);
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



