import f_button from "../../../../button";
import edit_select from "../../../../edit_select";
import tabbar from "@/components/tabbar.vue";
import edit_textarea from "../../../../edit_textarea";
import result from "../../../../result";
import top_progress from "../../../../top_progress";
import f_confirm from "../../../../confirm";
import f_table from "../../../../table";
export default {
  components: {
    top_progress,
    f_button,
    edit_select,
    edit_textarea,
    f_confirm,
    f_table,
    result,
    tabbar
  },
  data() {
    return {
      nav_bar_idx: -1,
      show_result: false,
      top_title: "",
      top_progress: {
        step: 0,
        list: [{ des: "" }, { des: "" }, { des: "" }]
      },
      edit_textarea: {
        required: true,
        title: "",
        placeholder: ""
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      edit_select: [
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "" }, { td: "" }, { td: "" }]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "1" }, { td: "" }, { td: "2" }]
            }
          ]
        }
      },
      table_info2: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [{ td: "", clo: 3 }]
            },
            {
              td_list: [{ td: "" }]
            }
          ]
        }
      },
      confirm_info: {
        title: "",
        content: "",
        show: false
      },
      result_info: {
        title: "",
        des:
          '<a href="javascript:void(0);">EO1809231814196051</a>2',
        buttons: [
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "default",
            text: "",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    get_call_back() {
      this.show_result = true;
    },
    confirm_back(res) {
      this.top_progress.step = 2;
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



