import list_item from "../../../list_item";
import f_button from "../../../button";
import actionsheet from "../../../actionsheet";
export default {
  name: "report_today",
  components: {
    list_item,
    f_button,
    actionsheet
  },
  data() {
    return {
      actionsheet_info: {
        show: false,
        list: {
          menu1: "",
          menu2: "",
          menu3: ""
        },
        call_back: true
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
     list: [
        {
          title: "0112",
          type: "",
          time: "2019-11-12 12:12:21",
          status: "",
          plan_text: "",
          status_color: "red",
          plan_color: "#199ed8"
        },
        {
          title: "0112",
          type: "",
          time: "2019-11-12 12:12:21",
          status: "",
          plan_text: "",
          status_color: "blue",
          plan_color: "#199ed8"
        },
        {
          title: "0112",
          type: "",
          time: "2019-11-12 12:12:21",
          status: "",
          status_color: "red",
          plan_text: "",
          plan_color: "#008000",
          hide_border:true
        }
      ]
    };
  },
  methods: {
    call_back(res) {
      this.actionsheet_info.show = true;
    },
    actionsheet_back(res) {
      if (res == "menu1") {
        this.go_page("/on_plan");
      } else if (res == "menu2") {
        this.go_page("/no_on_plan");
      }
    },
    go_page(url) {
      this.$router.push(url);
    }
  }
};



