import f_button from "@/components/button";
import edit_input from "@/components/edit_input";
import upload_file from "@/components/upload_file";
import edit_select from "@/components/edit_select";
import edit_textarea from "@/components/edit_textarea";
import result from "@/components/result";
import f_table from "@/components/table";
export default {
  components: {
    edit_input,
    f_button,
    upload_file,
    edit_select,
    edit_textarea,
    f_table,
    result
  },
  data() {
    return {
      upload_file_info: {
        text: "",
        required: true,
        class: "img40"
      },
      show_device_modal: false,
      show_result: false,
      edit_input_list: [
        { v: "", k: "", read: true },
        { v: "", k: "M767672", read: true }
      ],
      edit_textarea: {
        required: true,
        title: "",
        placeholder: ""
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      edit_select: [
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        }
      ],
      table_info: {
        show_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "", min_w: true },
                { td: "", td_104_w: true },
                { td: "" },
                { td: "", td_70_w: true },
                { td: "", td_70_w: true }
              ]
            }
          ]
        }
      },
      table_info1: {
        show_border: false,
        no_border: true,
        table: {
          tr_list: [
            {
              td_list: [
                { td: "1", min_w: true },
                { td: "", td_104_w: true },
                { td: "M767672000" },
                { td: "", td_70_w: true },
                { td: "2", td_70_w: true }
              ]
            }
          ]
        }
      },

      confirm_info: {
        title: "",
        content: "",
        show: false
      },
      result_info: {
        title: "",
        des:
          '<a href="javascript:void(0);">EO1809231814196051</a>2',
        buttons: [
          {
            type: "primary",
            text: "",
            link: "/"
          },
          {
            type: "default",
            text: "",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    hide_device_modal() {
      this.show_device_modal = false;
    },
    get_call_back() {
      this.show_result = true;
    },
    confirm_back(res) {},
    go_page(url) {
      this.$router.push(url);
    }
  }
};



