
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


export default {
components: {
PopupPicker
},
props: ["picker_info"],
data() {
return {
select_arr: []
};
},
methods: {
change(res) {
var list = this.picker_info.list,
new_arr = [],
s1 = res[0] == "new" ? "" : "",
s2 = res[1] == "on" ? "" : "",
s3 = "";
for (var i in list) {
if (res[2] == list[i].value) {
s3 = list[i].name;
}
}
new_arr.push(s1, s2, s3);
this.$emit("call_back", new_arr); 
}
}
};



