import list_item from "../../../../list_item";
import f_button from "../../../../button";
import f_search from "../../../../search";
import f_nav_bar from "../../../../nav_bar";
export default {
  name: "report_today",
  components: {
    list_item,
    f_button,
    f_search,
    f_nav_bar
  },
  data() {
    return {
      search: {
        placeholder: ""
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      nav_bar: {
        list: [
          {
            cur: true,
            text: ""
          }
        ]
      },

      list: [
        {
          title: "",
          type: "12/53",
          time: "2019-01-05",
          status: "",
          status_color: "blue",
        },
        {
          title: "",
          type: "12/53",
          time: "2019-01-05",
          status: "",
        },
        {
          title: "",
          type: "12/53",
          time: "2019-01-05",
          status: "",
          status_color: "blue",
          hide_border:true
        }
      ]
    };
  },
  methods: {
    call_back(res) {
      this.go_page("/check_info");
    },

    go_page(url) {
      this.$router.push(url);
    }
  }
};



