import edit_select from "../../../edit_select";
import edit_textarea from "../../../edit_textarea";
import f_confirm from "../../../confirm";
import loading from "../../../loading";
import f_button from "../../../button";
import result from "../../../result";
export default {
  components: {
    edit_select,
    edit_textarea,
    f_button,
    loading,
    f_confirm,
    result
  },
  data() {
    return {
      show_result: false,
      edit_select: [
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        },
        {
          title: "",
          default_val: "",
          required: true,
          placeholder: "",
          list: [
            { key: "", value: "" },
            { key: "1", value: "1" }
          ]
        }
      ],
      edit_textarea: {
        title: "",
        required: true,
        placeholder: ""
      },
      button_list: [
        {
          type: "primary",
          text: "",
          call_back: true
        }
      ],
      loading_info: {
        show: false,
        text: ",.."
      },
      confirm_info: {
        show: false,
        title: "",
        content: ""
      },
      result_info: {
        title: "",
        des:
          '<a href="javascript:void(0);">EO1809231814196051</a>',
        buttons: [
          {
            type: "primary",
            text: "",
            link: "BACK"
          },
          {
            type: "primary",
            text: "",
            link: "/report_today"
          },
          {
            type: "primary",
            text: "",
            link: "/"
          }
        ]
      }
    };
  },
  methods: {
    btn_call_back() {
      this.confirm_info.show = true;
    },
    confirm_back() {
      this.loading_info.show = true;
      setTimeout(() => {
        this.loading_info.show = false;
        this.show_result = true;
      }, 1000);
    }
  }
};



